    .page-body-wrapper {
      .page-body {
        position: relative;
        &:before {
          position: fixed;
          content: "";
          background-color: rgba(0, 0, 0, 0.5);
          height: 100%;
          width: 100%;
          z-index: 10;
          right: 0;
          left: 255px;
        }
      }
    }
