          width: 100px !important;
          &:nth-child(2) {
            width: 250px !important;
          }
          &:nth-child(3) {
            width: 120px !important;
          }
        }
      }
    }
  }
  .digital-product {
    table {
      tr {
        td,
        th {
          &:nth-child(2) {
            width: 120px !important;
          }
        }
      }
    }
  }
  // add product css
  .product-buttons {
    padding: 0 15px;
  }
  .add-product-form {
    .form-group {
      .form-control {
        width: 93%;
        margin: 0 auto;
      }
      label {
        padding: 0;
      }
    }
    .qty-box {
      width: 162px;
      .input-group {
        .form-control {
          width: 80px;
        }
      }
    }
    .description-sm {
      padding: 0 !important;
    }
  }
  .zoomContainer {
    top: 184px !important;
  }
  .add-product {
    ul {
      li {
        .box-input-file,
        .imgUpload {
          width: 30px;
          height: 30px;
        }
      }
    }
  }
  // order
  .dataTables_wrapper {
    .dataTables_paginate {
      margin-top: 15px !important;
      margin-left: 0 !important;
      .paginate_button {
        padding: 1px 4px !important;
      }
    }
    .dataTables_length {
      margin-bottom: 15px;
    }
    table {
      &.dataTable {
        margin-top: 15px !important;
      }
    }
  }
}
